import axios from "axios";


const addCategory = (name) => {
    return axios.post(`/Catigories/add/${name}`).then(({data}) => data);
}

const removeCategory = (name) => {
    return axios.post(`/Catigories/remove/${name}`).then(({data}) => data);
}

const editCategory = (prevName, newName) => {
    return axios.post(`/Catigories/editName?previousName=${prevName}&newName=${newName}`)
        .then(({data})=>data);
}

const getCategories = () => {
    return axios.get('/Catigories/GetAll').then(({data})=>data);
}

const categoriesApi = {
    addCategory,
    removeCategory,
    editCategory,
    getCategories
}

export default categoriesApi;